<template>
  <div>
    <div class="column">
      <h1 class="title is-2 level-item">{{this.nameCat.category.name}}</h1>
    </div>
    <br>
    <br>
    <div class="column">
      <div class="flex flex-wrap -mx-4 is-full-mobile">
        <div class="lg:w-1/3 w-full px-6 mb-12">
          <div class="box is-5-desktop">
            <div class="column">
              <h2 class="title is-4">Fitros</h2>
              <div class="card-footer"></div><br>
              <label for="cidade">
                Filtrar Por Cidade
              </label><div class="form"></div>
              <input
                type="text"
                class="input inp column is-12-desktop"
                v-model="cidade"
                placeholder="Ex:. Floriano"
                name="cidade"
                id="cidade"
              >
            </div>
            <div class="column">
              <label for="cidade">
                Filtrar Por Estado
              </label><div class="form"></div>
              <input
                type="text"
                class="input inp column is-12-desktop"
                v-model="estado"
                placeholder="Ex:. Piauí"
                name="estado"
                id="estado"
              >
            </div>
          </div>
        </div>
        <div class="lg:w-2/3 w-full px-6 mb-8">
          <div v-if="this.cidade === '' && this.estado === ''">
            <ApolloQuery
              :query="require('@/graphql/queries/proByCategory.gql')"
              :variables="{category_id:this.$route.params.id}"
            >
              <template slot-scope="{ result: { data, loading },isLoading }">
                <div v-if="isLoading">Loading...</div>
                <div v-else>
                  <div class="flex flex-wrap items-center justify-between">
                    <div
                      v-for="cat of data.proByCategory"
                      :key="cat.id"
                      class="lg:w-1/3 w-full is-full-mobile px-1 mb-5"
                    >
                      <div>
                        <div class="card">
                          <div class="column" align="center">
                              <img  class="img" width="55%" height="200%"
                                :src="`http://graphql.me/imagem/${cat.imagem}`"
                                alt="Placeholder image"
                              >
                          </div>
                          <div class="">
                            <div align="center" class="text-black font-bold">{{cat.user.name}}</div>
                            <div align="center" class="text-black">{{cat.ramo}}</div>
                            <div align="center" class="text-black">{{cat.cidade}}, {{cat.estado}}</div>
                          </div>
                          <ApolloQuery
                            :query="require('@/graphql/queries/mediaAvaliacao.gql')"
                            :variables="{user_avaliado:cat.id}"
                          >
                            <template slot-scope="{ result: { data, loading },isLoading }">
                              <div v-if="isLoading">Loading...</div>
                              <div v-else>
                                      <div v-if="data.mediaAvaliacao !== null" align="center">
                                          <div v-if="data.mediaAvaliacao === 5"><img class="column is-10-desktop is-6-mobile" src="../../assets/estrela5.png" alt="cover image"></div>
                                          <div v-if="data.mediaAvaliacao >= 4 && data.mediaAvaliacao < 5"><img class="column is-10-desktop is-6-mobile" src="../../assets/estrela4.png" alt="cover image"></div>
                                          <div v-if="data.mediaAvaliacao >= 3 && data.mediaAvaliacao < 4"><img class="column is-10-desktop is-6-mobile" src="../../assets/estrela3.png" alt="cover image"></div>
                                          <div v-if="data.mediaAvaliacao >= 2 && data.mediaAvaliacao < 3"><img class="column is-10-desktop is-6-mobile" src="../../assets/estrela2.png" alt="cover image"></div>
                                          <div v-if="data.mediaAvaliacao < 2"><img class="" src="../../assets/estrela1.png" alt="cover image"></div>
                                      </div>
                                      <div v-else>
                                        <div><img class="column is-10-desktop is-6-mobile" src="../../assets/estrelaVazia.png" alt="cover image"></div>
                                      </div>
                                    </div>
                            </template>
                          </ApolloQuery>
                          <div class="column">
                              <router-link
                                :to="`/page/cliente/categories/profissional/${cat.id}`"
                                class="level-item"
                              > 
                                <button class="btn-9 column">Ver perfil</button>
                              </router-link>
                          </div>
                            
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </template>
            </ApolloQuery>
          </div>
          <div v-else>
            <ApolloQuery
              :query="require('@/graphql/queries/searchWithCat.gql')"
              :variables="{category:this.$route.params.id,cidade:this.cidade,estado:''+this.estado}"
            >
              <template slot-scope="{ result: { data, loading },isLoading }">
                <div v-if="isLoading">Loading...</div>
                <div v-else>
                  <div class="flex flex-wrap items-center justify-between">
                    <div
                      v-for="cat of data.searchWithCat"
                      :key="cat.id"
                      class="lg:w-1/3 w-full is-full-mobile px-1 mb-5"
                    >
                      <div>
                        <div class="card">
                           <div class="column" align="center">
                              <img  class="img" width="55%" height="200%"
                                :src="`http://graphql.me/imagem/${cat.imagem}`"
                                alt="Placeholder image"
                              >
                          </div>
                          <div class="">
                            <div align="center" class="text-black font-bold">{{cat.user.name}}</div>
                            <div align="center" class="text-black">{{cat.ramo}}</div>
                            <div align="center" class="text-black">{{cat.cidade}}, {{cat.estado}}</div>
                          </div><br>
                          <footer class="card-footer column">
                            <router-link
                              :to="`/page/cliente/categories/profissional/${cat.id}`"
                              class="level-item"
                            >
                              <button class="btn-9">Ver detalhes desse profissional</button>
                            </router-link>
                          </footer>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </template>
            </ApolloQuery>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import categoryName from "@/graphql/queries/nameCategory.gql";

export default {
  data() {
    return {
      nameCat: [],
      cidade: "",
      estado: ""
    };
  },
  apollo: {
    nameCat: {
      query: categoryName,
      variables() {
        return {
          id: this.$route.params.id
        };
      },
      update(data) {
        this.nameCat = data;
        return this.nameCat;
      }
    }
  }
};
</script>

<style>
@import "../../cssFiles/inputsAndButtons.css";

.form{  
  margin-bottom: 5px;
}

.inp {
  width: 100%;
  height: 3.5em;
  padding: 12px 20px;
  box-sizing: border-box;
  border: 3px solid #ccc;
  -webkit-transition: 0.5s;
  transition: 0.5s;
  outline: none;
}
.inp:focus {
  border: 3px solid tomato;
}

.inp1 {
  width: 100%;
  padding: 12px 20px;
  box-sizing: border-box;
  border: 3px solid #ccc;
  -webkit-transition: 0.5s;
  transition: 0.5s;
  outline: none;
}
.inp1:focus {
  border: 3px solid tomato;
}

.img{
  border-radius: 50%;
}

</style>